.search-result-box .search-item {
  padding-bottom: 20px;
  border-bottom: 1px solid #f1f5f7;
  margin-bottom: 20px;
}

div.mt-4.search-result-box > div > div > em,
div.mt-4.search-result-box > div > p > em {
  font-weight: 600;
}

div.mt-4.search-result-box > div > h4 > a {
  color: #3bafda;
}

div.mt-4.search-result-box > div > h4 > a:hover {
  color: #2084a9;
}

.text-success {
  color: #1abc9c !important;
}
